@import url("https://fonts.googleapis.com/css2?family=Barlow:wght@600&family=Fraunces:wght@700;900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  position: relative;
  font-family: "Barlow", sans-serif;
  font-size: 1.125rem;
}

nav ul {
  list-style-type: none;
}

img {
  display: block;
  width: 100%;
  object-position: center;
  object-fit: cover;
}

button {
  display: block;
  cursor: pointer;
  background-color: transparent;
  border: 0;
}

header {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.875rem 1.5625rem;
}

.header-logo {
  width: 7.8125rem;
}

.header-nav {
  position: absolute;
  opacity: 0;
  visibility: hidden;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-image: url("/images/mobile/image-header.jpg");
  background-position: center;
  background-size: cover;
}
main h1 {
  font-family: "Fraunces", serif;
  color: white;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.46875rem;
  margin: 0 0.625rem;
  margin-bottom: 3.75rem;
  font-weight: 900;
  font-size: 2.5rem;
}

.arrow-down {
  height: 9.375rem;
  width: auto;
}

.section--articles {
  display: grid;
  justify-items: center;
}
.section--articles article {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  line-height: 1.875rem;
  max-width: 25rem;
  padding: 3.75rem 1.25rem;
  gap: 1.875rem;
}
.section--articles article h2 {
  font-family: "Fraunces", serif;
  line-height: 2.8125rem;
}
.section--articles > article h2 {
  font-size: 2.1875rem;
  color: #23303e;
}
.section--articles > article p {
  color: #818498;
}
.section--articles > article button {
  position: relative;
  font-family: "Fraunces", serif;
  font-size: 1.0625rem;
  text-transform: uppercase;
}
.section--articles > article button::after {
  content: "";
  position: absolute;
  bottom: -0.21875rem;
  left: -0.46875rem;
  right: -0.46875rem;
  height: 0.625rem;
  border-radius: 0.4375rem;
  z-index: -1;
}
.section--articles > div {
  position: relative;
}
.section--articles > div:nth-of-type(1) {
  color: #25564b;
}
.section--articles > div:nth-of-type(2) {
  color: #19536b;
}
.section--articles > div > article {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
}

.btn--yellow::after {
  background-color: rgba(250, 212, 0, 0.25);
}

.btn--pink::after {
  background-color: rgba(254, 120, 103, 0.25);
}

.section--testimonials {
  padding: 3.75rem 1.875rem;
}
.section--testimonials > h3 {
  font-family: "Fraunces", serif;
  color: #a7abae;
  letter-spacing: 0.3125rem;
  font-size: 1.125rem;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 3.5rem;
}

.testimonials {
  display: grid;
  gap: 4rem;
}

.testimonial {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.testimonial img,
.testimonial p {
  margin-bottom: 2.5rem;
}
.testimonial p {
  text-align: center;
  color: #5a636c;
  line-height: 2.1875rem;
}
.testimonial h3 {
  font-family: "Fraunces", serif;
  color: #23303e;
  margin-bottom: 0.9375rem;
}
.testimonial h4 {
  color: #a7abae;
  font-size: 1rem;
}

.profileImg {
  width: 4.375rem;
  height: 4.375rem;
  border-radius: 50%;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
}

footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3.75rem 1.875rem 1.875rem 1.875rem;
  background-color: #458c7e;
}
footer nav {
  max-width: 23.4375rem;
  width: 100%;
}
footer nav ul {
  display: grid;
  grid-auto-flow: column;
  justify-content: space-between;
  color: #25564b;
}

.footer-logo {
  width: 10.9375rem;
  margin-bottom: 2.5rem;
  filter: invert(28%) sepia(5%) saturate(4119%) hue-rotate(116deg) brightness(30%) contrast(85%);
}

.socials {
  display: flex;
  margin-top: 5rem;
  gap: 1.875rem;
}

.social {
  width: auto;
  filter: brightness(50%);
}